.navbar{
    padding: 0;

}
.navbar .navbar-brand{
    font-size: 30px;
    font-weight: 900;
    letter-spacing: -2px;
}
.navbar .navbar-brand::after{
    content: '';
    height: 10px;
    width: 135px;
    background-color: red;
    display: block;
    top: 35px;
    position: absolute;
    opacity: 0.8;
}
.navbar .collapse ul li p {
    font-weight: bold;
    color : #6d6d6d
}
